img {
  width: 100%;
}

.landing {
  position: relative;
  background: url(/culture-portal/static/media/showcase.4b31330b.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}

#mapid {
  height: 380px;
}

nav {
  width:100%;
  padding: 5px;
  display: flex;
  justify-content: space-evenly;
  background: rgb(23,36,48);  
}

.nav-button {
  width: 200px;
  background: #007bff;
  border: #007bff solid;
  border-radius: .25rem;
  padding: 3px;
  list-style-type: none;
  cursor: pointer;
  text-align: center;
  font-size: 23px;
  color: whitesmoke;
}

.nav-button:hover {
  border: silver solid;
}
